'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Loading from '@/components/Loading';
import api from '@/lib/api';
import { FiCalendar, FiUser, FiFileText, FiClock, FiBell, FiSearch, FiMessageCircle } from 'react-icons/fi';
import { format, parseISO } from 'date-fns';
import PatientSidebar from '@/components/PatientSidebar';

export default function DashboardPage() {
  const { user, loading: authLoading, logout } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!authLoading && !user) {
      router.push('/login');
    }
  }, [user, authLoading, router]);

  useEffect(() => {
    if (user) {
      if (user.role === 'doctor') {
        checkDoctorProfile();
        // Redirect doctors to appointments page (doctor portal)
        router.push('/appointments');
        return;
      } else {
        // Force fresh data fetch for patients
        fetchDashboardData();
      }
    }
  }, [user]);

  const checkDoctorProfile = async () => {
    try {
      const response = await api.get('/doctors/profile/me');
      if (!response.data.doctor || response.data.doctor.status === 'pending') {
        // Redirect to complete profile if not completed or pending
        router.push('/doctor/complete-profile');
      } else if (response.data.doctor.status === 'approved') {
        fetchDashboardData();
      }
    } catch (error) {
      // If no profile exists, redirect to complete profile
      router.push('/doctor/complete-profile');
    }
  };

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      if (user?.role === 'admin') {
        const response = await api.get('/admin/dashboard');
        setStats(response.data);
      } else if (user?.role === 'doctor') {
        const appointments = await api.get('/appointments?limit=5');
        const prescriptions = await api.get('/prescriptions?limit=5');
        setStats({
          appointments: appointments.data.appointments,
          prescriptions: prescriptions.data.prescriptions,
        });
      } else {
        // Fetch patient's own appointments and prescriptions with fresh data
        const appointments = await api.get(`/appointments?patientId=${user?.id}&limit=5`);
        const prescriptions = await api.get(`/prescriptions?limit=5`);
        setStats({
          appointments: appointments.data.appointments || [],
          prescriptions: prescriptions.data.prescriptions || [],
        });
      }
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
      // Set empty stats on error
      setStats({
        appointments: [],
        prescriptions: [],
      });
    } finally {
      setLoading(false);
    }
  };

  const getInitials = (name: string) => {
    return name
      ?.split(' ')
      ?.map((n: string) => n[0])
      ?.join('')
      ?.toUpperCase()
      ?.slice(0, 2) || 'U';
  };

  if (authLoading || loading) {
    return <Loading />;
  }

  if (!user) {
    return null;
  }

  // Patient Dashboard with Teal Sidebar Design
  if (user.role === 'patient') {
    return (
      <div className="flex min-h-screen bg-gray-50">
        {/* Collapsible Left Sidebar */}
        <div className={`${sidebarCollapsed ? 'w-16' : 'w-64'} bg-teal-600 text-white transition-all duration-300 fixed left-0 top-0 h-full z-20`}>
          <div className="p-4">
            {/* Collapse/Expand Button */}
            <button
              onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
              className="w-full flex items-center justify-center p-2 hover:bg-teal-700 rounded-lg transition-colors mb-4"
            >
              <FiChevronRight className={`text-xl transform transition-transform ${sidebarCollapsed ? '' : 'rotate-180'}`} />
            </button>

            {/* Medical Symbol / Logo */}
            {!sidebarCollapsed && (
              <div className="flex items-center justify-center mb-6">
                <div className="flex items-center gap-2">
                  <span className="text-2xl">ðŸ©º</span>
                  <span className="text-xl font-bold">MediWise</span>
                </div>
              </div>
            )}

            {/* Patient Profile Section */}
            {!sidebarCollapsed && (
              <div className="mb-6 pb-6 border-b border-teal-500">
                <div className="flex items-center gap-3 mb-3">
                  <div className="w-10 h-10 rounded-full bg-white bg-opacity-20 flex items-center justify-center text-sm font-semibold">
                    {getInitials(user.name || 'P')}
                  </div>
                  <div className="flex-1 min-w-0">
                    <p className="text-sm font-semibold truncate">{user.name}</p>
                    <p className="text-xs text-teal-200 capitalize">Patient</p>
                  </div>
                </div>
              </div>
            )}

            {/* Navigation Links */}
            <nav className="space-y-1">
              {[
                { href: '/dashboard', label: 'Dashboard', icon: FiHome, active: true },
                { href: '/doctors', label: 'Doctors', icon: FiUsers },
                { href: '/appointments', label: 'Appointments', icon: FiCalendar },
                { href: '/patient/chat', label: 'Chats', icon: FiMessageCircle },
                { href: '/prescriptions', label: 'Prescriptions', icon: FiFileText },
                { href: '/profile', label: 'My Profile', icon: FiUser },
              ].map((item) => {
                const Icon = item.icon;
                const isActive = item.active;
                return (
                  <Link
                    key={item.href}
                    href={item.href}
                    className={`flex items-center gap-3 px-4 py-3 rounded-lg transition-colors ${
                      isActive
                        ? 'bg-teal-700 text-white font-semibold'
                        : 'text-teal-100 hover:bg-teal-700 hover:text-white'
                    }`}
                  >
                    <Icon className="text-xl flex-shrink-0" />
                    {!sidebarCollapsed && <span className="text-sm">{item.label}</span>}
                  </Link>
                );
              })}
            </nav>

            {/* Logout Button */}
            {!sidebarCollapsed && (
              <div className="absolute bottom-4 left-4 right-4">
                <button 
                  onClick={logout}
                  className="w-full flex items-center gap-3 px-4 py-3 rounded-lg text-teal-100 hover:bg-teal-700 transition-colors"
                >
                  <FiLogOut className="text-xl" />
                  <span className="text-sm">LOGOUT</span>
                </button>
              </div>
            )}
          </div>
        </div>

        {/* Main Content */}
        <main className={`${sidebarCollapsed ? 'ml-16' : 'ml-64'} flex-1 transition-all duration-300`}>
          {/* Header */}
          <header className="bg-white border-b border-gray-200 px-8 py-4 flex items-center justify-between">
            <h1 className="text-2xl font-bold text-gray-800 uppercase tracking-wide">
              Welcome back, {user.name}!
            </h1>
            <div className="flex items-center gap-4">
              <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <FiSearch className="text-xl text-gray-600" />
              </button>
              <Link href="/patient/chat" className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <FiMessageCircle className="text-xl text-gray-600" />
                <span className="absolute top-0 right-0 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center">
                  2
                </span>
              </Link>
              <button className="relative p-2 hover:bg-gray-100 rounded-lg transition-colors">
                <FiBell className="text-xl text-gray-600" />
                <span className="absolute top-0 right-0 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center">
                  2
                </span>
              </button>
              <div className="w-10 h-10 rounded-full bg-gradient-to-br from-teal-400 to-indigo-500 flex items-center justify-center text-white font-bold">
                {getInitials(user?.name || 'P')}
              </div>
            </div>
          </header>

          {/* Content */}
          <div className="p-8">
            <div className="bg-white rounded-xl shadow-sm p-6 border border-gray-100 mb-6">
              <h2 className="text-xl font-semibold text-gray-800 mb-4">Recent Appointments</h2>
              {stats?.appointments && stats.appointments.length > 0 ? (
                <div className="space-y-4">
                  {stats.appointments.map((appt: any) => (
                    <div
                      key={appt.id}
                      className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow"
                    >
                      <div className="flex justify-between items-start">
                        <div className="flex-1">
                          <p className="font-semibold text-gray-800 text-lg">
                            Dr. {appt.doctor?.user?.name || 'Doctor'}
                          </p>
                          <p className="text-sm text-gray-600 mt-2">
                            {format(parseISO(appt.appointmentDate), 'MM/dd/yyyy')} at {appt.appointmentTime}
                          </p>
                          {appt.reason && (
                            <p className="text-sm text-gray-700 mt-2 font-medium">{appt.reason}</p>
                          )}
                        </div>
                        <span
                          className={`px-3 py-1 rounded-full text-xs font-semibold ${
                            appt.status === 'confirmed'
                              ? 'bg-green-100 text-green-800'
                              : appt.status === 'pending'
                              ? 'bg-yellow-100 text-yellow-800'
                              : appt.status === 'completed'
                              ? 'bg-blue-100 text-blue-800'
                              : 'bg-red-100 text-red-800'
                          }`}
                        >
                          {appt.status}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-gray-500 text-center py-8">No appointments yet</p>
              )}
            </div>
          </div>
        </main>
      </div>
    );
  }

  // Admin Dashboard (existing design)
  return (
    <div className="flex min-h-screen">
      <div className="fixed left-0 top-0 h-full w-64 bg-white shadow-lg z-10">
        <div className="p-6 border-b border-gray-200">
          <h1 className="text-2xl font-bold text-teal-600">ðŸ©º MediWise</h1>
        </div>
        <nav className="p-4 space-y-2">
          <Link href="/dashboard" className="flex items-center gap-3 px-4 py-3 rounded-lg bg-teal-50 text-teal-600 font-medium">
            <FiHome className="text-xl" />
            <span>Dashboard</span>
          </Link>
        </nav>
      </div>
      <main className="ml-64 flex-1 p-8">
        <div className="max-w-7xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-800 mb-6">
            Welcome back, {user.name}!
          </h1>

          {user.role === 'admin' && stats && (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Total Users</p>
                    <p className="text-3xl font-bold text-teal-600 mt-2">{stats.stats?.totalUsers || 0}</p>
                  </div>
                  <FiUser className="text-4xl text-teal-600" />
                </div>
              </div>
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Total Doctors</p>
                    <p className="text-3xl font-bold text-indigo-600 mt-2">{stats.stats?.totalDoctors || 0}</p>
                  </div>
                  <FiUser className="text-4xl text-indigo-600" />
                </div>
              </div>
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Appointments</p>
                    <p className="text-3xl font-bold text-teal-600 mt-2">{stats.stats?.totalAppointments || 0}</p>
                  </div>
                  <FiCalendar className="text-4xl text-teal-600" />
                </div>
              </div>
              <div className="bg-white rounded-lg shadow-md p-6">
                <div className="flex items-center justify-between">
                  <div>
                    <p className="text-gray-600 text-sm">Pending</p>
                    <p className="text-3xl font-bold text-orange-600 mt-2">{stats.stats?.pendingAppointments || 0}</p>
                  </div>
                  <FiClock className="text-4xl text-orange-600" />
                </div>
              </div>
            </div>
          )}
        </div>
      </main>
    </div>
  );
}
